import React from 'react';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';

const Settings = props => (
	<Panel id={props.id}>
		<PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
			Настройки
        </PanelHeader>
		<div className="stage" style={{
			width: "100%",
			height: "100%",
			position: "fixed",
			display: "flex",
			justifyContent: "center"
		}}>
			<Div>
				<Button style={{ color: "white", border: "1px solid white" }} onClick={() => props.camelCase(props.scheme, true)} size='xl' mode='outline'>Сменить тему на альтернативную</Button>
			</Div>
		</div>
		
	</Panel>
);

export default Settings;