import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ConfigProvider from '@vkontakte/vkui/dist/components/ConfigProvider/ConfigProvider';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Play from './panels/Play';
import Settings from './panels/Settings';
import Rating from './panels/Rating';
import Info from './panels/Info';

const lights = ['bright_light', 'client_light'];

const App = () => {
	const [activePanel, setActivePanel] = useState("Home"); // Ставим начальную панель
	const [history, setHistory] = useState(['Home']) // Заносим начальную панель в массив историй.

	function goToPage(name) { // В качестве аргумента принимаем id панели для перехода
		window.history.pushState({ panel: name }, name); // Создаём новую запись в истории браузера
		setActivePanel(name); // Меняем активную панель
		history.push(name); // Добавляем панель в историю
	};

	const goBack = () => {
		setStateTimer("");
		setStateCount(100);
		setStaterightCityDictance();
		setStateleftCityDictance();
		if (history.length === 1) {  // Если в массиве одно значение:
			bridge.send("VKWebAppClose", { "status": "success" }); // Отправляем bridge на закрытие сервиса.
		} else if (history.length > 1) { // Если в массиве больше одного значения:
			history.pop() // удаляем последний элемент в массиве.
			setActivePanel(history[history.length - 1]) // Изменяем массив с иторией и меняем активную панель.
		}
	}
	

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				camelCase(data.scheme)
			}
		});
		window.addEventListener('popstate', () => goBack()); // Обработчик события изменения браузерной истории
	}, []);


	

	const [scheme, SetStateScheme] = useState('bright_light'); // Ставим цветовую схему по умолчанию.

	function camelCase(scheme, needChange = false) {
		let isLight = lights.includes(scheme);

		if (needChange) {
			isLight = !isLight;
		}
		SetStateScheme(isLight ? 'bright_light' : 'space_gray');

		bridge.send('VKWebAppSetViewSettings', {
			'status_bar_style': isLight ? 'dark' : 'light',
			'action_bar_color': isLight ? '#ffffff' : '#191919'
		});
	}
	const [response, setStateResponse] = useState("false");

	const [count, setStateCount] = useState(100);
	const [timer, setStateTimer] = useState(" ");

	const [leftCity, setStateLeftCity] = useState("Tokio");
	const [rightCity, setStateRightCity] = useState("New-York");
	const [leftCityDictance, setStateleftCityDictance] = useState("");
	const [rightCityDictance, setStaterightCityDictance] = useState("");
	const [dictanceLeft, setStateDictanceLeft] = useState("");
	const [dictanceRight, setStateDictanceRight] = useState("");
	const [checkRightBut, setStateCheckRightBut] = useState("");
	const [checkLeftBut, setStateCheckLeftBut] = useState("");
	

	let arrCityLeft = [
		 [["city", "Уфа"], ["lat", 54.7431], ["long", 55.9678]] ,
		 [["city", "Шанхай"], ["lat", 31.23042], ["long", 121.4737]] ,
		 [["city", "Мехико"], ["lat", 19.4285], ["long", -99.1277]],
		 [["city", "Сан-Паулу"], ["lat", -23.55052], ['long', -46.63331]],
		 [["city", "Каир"], ["lat", 30.0626], ["long", 31.2497]],
		 [["city", "Мумбаи"], ["lat", 19.07598], ["long", 72.87766]],
		 [["city", "Дакка"], ["lat", 23.81033], ["long", 90.41252]],
		 [["city", "Осака"], ["lat", 34.6812515], ["long", 135.5314026]],
		 [["city", "Карачи"], ["lat", 24.86146], ["long", 67.00994]],
		 [["city", "Стамбул"], ["lat", 41.0138], ["long", 28.9497]],
		 [["city", "Москва"], ["lat", 55.7522], ["long", 37.6156]],
	];

	let arrCityRight = [
		[["city", "Уфа"], ["lat", 54.7431], ["long", 55.9678]],
		[["city", "Шанхай"], ["lat", 31.23042], ["long", 121.4737]],
		[["city", "Мехико"], ["lat", 19.4285], ["long", -99.1277]],
		[["city", "Сан-Паулу"], ["lat", -23.55052], ['long', -46.63331]],
		[["city", "Каир"], ["lat", 30.0626], ["long", 31.2497]],
		[["city", "Мумбаи"], ["lat", 19.07598], ["long", 72.87766]],
		[["city", "Дакка"], ["lat", 23.81033], ["long", 90.41252]],
		[["city", "Осака"], ["lat", 34.6812515], ["long", 135.5314026]],
		[["city", "Карачи"], ["lat", 24.86146], ["long", 67.00994]],
		[["city", "Стамбул"], ["lat", 41.0138], ["long", 28.9497]],
		[["city", "Москва"], ["lat", 55.7522], ["long", 37.6156]],
	];

	function startGame() {
		setStateResponse(false)
		setStateCheckLeftBut(false)
		setStateCheckRightBut(false)
		let timerId = setInterval(() => {
			
			var randRight = Math.floor(Math.random() * arrCityRight.length);
			var randLeft = Math.floor(Math.random() * arrCityLeft.length);
			let arrLeft = Object.fromEntries(arrCityLeft[randLeft]);
			let arrRight = Object.fromEntries(arrCityRight[randRight]);
			setStateRightCity(arrRight.city)
			setStateLeftCity(arrLeft.city)

		}, 60);

		
		setTimeout(() => { clearInterval(timerId); countDown(); }, 3000);
	
}

	function countDown() {
		setStateResponse(false)
		setStateCheckLeftBut(false)
		setStateCheckRightBut(false)
		var randRightCity = Math.floor(Math.random() * arrCityRight.length);
		let arrRight = Object.fromEntries(arrCityRight[randRightCity]);

		var randLeftCity = Math.floor(Math.random() * arrCityLeft.length);
		let arrLeft = Object.fromEntries(arrCityLeft[randLeftCity]);

		setStateRightCity(arrRight.city)
		setStateLeftCity(arrLeft.city)

		bridge.send("VKWebAppGetGeodata")
			.then(data => {

				function getDistanceFromLatLonInKmRight(lat4, lon4, lat8, lon8) {
					var R1 = 6371;
					var dLat1 = deg2rad(lat8 - lat4);
					var dLon1 = deg2rad(lon8 - lon4);
					var a1 =
						Math.sin(dLat1 / 2) * Math.sin(dLat1 / 2) +
						Math.cos(deg2rad(lat4)) * Math.cos(deg2rad(lat8)) *
						Math.sin(dLon1 / 2) * Math.sin(dLon1 / 2)
						;
					var c1 = 2 * Math.atan2(Math.sqrt(a1), Math.sqrt(1 - a1));
					var d1 = R1 * c1;
					return d1;
				}
				function deg2rad(deg1) {
					return deg1 * (Math.PI / 180)
				}

				function getDistanceFromLatLonInKmLeft(lat1, lon1, lat2, lon2) {
					var R = 6371;
					var dLat = deg2rad(lat2 - lat1);
					var dLon = deg2rad(lon2 - lon1);
					var a =
						Math.sin(dLat / 2) * Math.sin(dLat / 2) +
						Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) *
						Math.sin(dLon / 2) * Math.sin(dLon / 2)
						;
					var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
					var d = R * c;
					return d;
				}

				function deg2rad(deg) {
					return deg * (Math.PI / 180)
				}

				let i = 100
				let t = 4
				let timerIdcountDown = setInterval(() => {
					if (+getDistanceFromLatLonInKmRight(data.lat, data.long, arrRight.lat, arrRight.long).toFixed(0) < +getDistanceFromLatLonInKmLeft(data.lat, data.long, arrLeft.lat, arrLeft.long).toFixed(0)) {

						setStateCheckRightBut(true)
						setStateCheckLeftBut(false)
					
					} else {
						setStateCheckLeftBut(true)
						setStateCheckRightBut(false)
						
					}
					setStateCount(i = i - 20)
					setStateTimer("Осталось " + t-- + " сек")
					console.log(response);
					if (response === true) {
						setTimeout(() => {
							setTimeout(() => {
								clearInterval(timerIdcountDown); 
								setStateTimer("");
								setStateCount(100); 
							}, 0);
						}, 0);
					}
				}, 1000);

				setTimeout(() => {
					clearInterval(timerIdcountDown);
					setStateTimer("");
					setStateCount(100);
					checkResponse();
					setStaterightCityDictance(getDistanceFromLatLonInKmRight(data.lat, data.long, arrRight.lat, arrRight.long).toFixed(0) + " км");
					setStateleftCityDictance(getDistanceFromLatLonInKmLeft(data.lat, data.long, arrLeft.lat, arrLeft.long).toFixed(0) + " км");
				}, 5000);
				
				
			})


			.catch(error => {
				console.log(error);
			});
			
		
	}

	

	function userResponse() {
		setStateResponse(true)
		setStateTimer("");
		setStateCount(100);	
	}

 
	function checkResponse () {
		
		setStateResponse(false)
	}

	return (
		<ConfigProvider isWebView={true} scheme={scheme}>
			<View
				activePanel={activePanel} // Активная панель равная стейту.
				history={history} // Ставим историю из массива панелей.
				onSwipeBack={goBack} // При свайпе выполняется данная функция.
			>
				<Home id="Home" goToPage={goToPage} startGame={startGame} camelCase={camelCase} scheme={scheme}/>
				<Play id="Play" startGame={startGame} 
					leftCityDictance={leftCityDictance}
					dictanceLeft={dictanceLeft}
					dictanceRight={dictanceRight}
					rightCityDictance={rightCityDictance}
					response={response} 
					leftCity={leftCity}
					rightCity={rightCity} 
					count={count}
					timer={timer}
					userResponse={userResponse} />
				<Settings id="Settings" camelCase={camelCase} scheme={scheme} />
				<Rating id="Rating" />
				<Info id="Info"/>
			</View>
		</ConfigProvider>
	);
}

export default App;