import React from 'react';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Group from '@vkontakte/vkui/dist/components/Group/Group';

import Icon48Play from '@vkontakte/icons/dist/48/play';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon28GlobeOutline from '@vkontakte/icons/dist/28/globe_outline';
import Icon28ErrorOutline from '@vkontakte/icons/dist/28/error_outline';

import './Style.css';

const home = (props) => {
	const start = () => {
		props.goToPage('Play')
		props.startGame()
	}

	return(
	<Panel id={props.id}>
		<PanelHeader>
			Home
        </PanelHeader>
		<div className="stage" style={{
			width: "100%",
			height: "100%",
			position: "fixed",
			display: "flex", 
			justifyContent: "center"
			}}>
			<div className="sun"></div>
			<div className="grass"></div>
				<h1 style={{ color: "white", fontSize: 45, fontFamily: 'Neucha'}}>Town</h1>
				<Button className="startButton" before={<Icon48Play style={{ color: "white" }} />} mode="outline" size="xl" onClick={() => start()}>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',

					}}>Играть!</p>
				</Button>
				<Button
					className="Settings"
					before={<Icon28SettingsOutline width={40} height={40} style={{ color: "white" }} />}
					mode="outline"
					size=""
					onClick={() => props.goToPage('Settings')}
				/>
				<Button
					className="Rating"
					before={<Icon28GlobeOutline width={40} height={40} style={{ color: "white" }} />}
					mode="outline"
					size=""
				onClick={() => props.goToPage('Rating')}
				>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',

					}}></p>
				</Button>
				<Button
					className="About"
					before={<Icon28ErrorOutline width={40} height={40} style={{ color: "white" }} />}
					mode="outline"
					data-to="persik"
					size=""
				onClick={() => props.goToPage('Info')}

				>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',

					}}></p>
				</Button>
			</div>
	</Panel>
)};

export default home;